import { Module } from "vuex";
import { RootState } from "..";

export interface GuiVuex {
  openDrawer: boolean;
  showSearch: boolean;
}

export const guiModule: Module<GuiVuex, RootState> = {
  state: {
    openDrawer: false,
    showSearch: false
  },
  mutations: {
    toggleDrawer(state, openDrawer = !state.openDrawer) {
      state.openDrawer = openDrawer;
    },
    toggleShowSearch(state, showSearch = !state.showSearch) {
      state.showSearch = showSearch;
    },
  },
  actions: {
    //
  },
};

export const
  TOGGLE_DRAWER = "toggleDrawer", TOGGLE_SHOW_SEARCH = "toggleShowSearch";
